class PainterApp {
  constructor() {
    this.image1 = new HTMLCanvasElement();
    this.image2 = new HTMLCanvasElement();
    document.body.appendChild(this.image1);
    document.body.appendChild(this.image2);
    this.srcimg = new Image();
    this.srcimg.onload = this.start().bind(this);
    this.srcimg.src =
      "https://d16kd6gzalkogb.cloudfront.net/magazine_images/James-McNeill-Whistler-Arangement-in-grey-and-black-No.-1-1871.jpg";
  }

  start() {
    const ctx = this.image2.getContext("2d");
    // // create backing canvas
    // var backCanvas = document.createElement('canvas');
    // backCanvas.width = canvas.width;
    // backCanvas.height = canvas.height;
    // var backCtx = backCanvas.getContext('2d');

    // // save main canvas contents
    // backCtx.drawImage(canvas, 0,0);

    // restore main canvas
    ctx.drawImage(this.srcimg, 0, 0);
  }
}

const painter = new PainterApp();
